require('../css/common.scss');
import React from "react";
import { Toast, WhiteSpace, WingBlank, Button, List, Picker } from 'antd-mobile';
import { createForm } from 'rc-form';
const district=[
				{
	            	value: '340000',
	            	label: '',
	            	children:[
		              	{
			                value: '340800',
			                label: '',
			                children:[
			                	{
				  	              value: '340824',
				  	              label: '',
				  	            }
			                ]
			            }
		            ]
	            },
	            {
	            	value: '34000',
	            	label: '',
	            	children:[
		              	{
			                value: '40800',
			                label: '',
			                children:[
			                	{
				  	              value: '34024',
				  	              label: '',
				  	            }
			                ]
			            }
		            ]
	            }
            ];
let Test = React.createClass({
  getInitialState() {
    return {
      data: [],
      cols: 1,
    };
  },
  onClick() {
    setTimeout(() => {
      this.setState({
        data: district,
      });
      
    }, 500);
  },
  onPickerChange(val) {
    console.log(val);
    let colNum = 1;
    const d = [...this.state.data];
    if (val[0] === 'zj') {
      d.forEach(i => {
        if (i.value === 'zj') {
          colNum = 2;
          if (!i.children) {
            i.children = [{
              value: 'zj-nb',
              label: '',
            }, {
              value: 'zj-hz',
              label: '',
            }];
          } else if (val[1] === 'zj-hz') {
            i.children.forEach(j => {
              if (j.value === 'zj-hz') {
                j.children = [{
                  value: 'zj-hz-xh',
                  label: '',
                }];
              }
            });
            colNum = 3;
          }
        }
      });
    } else {
      colNum = 1;
    }
    this.setState({
      data: d,
      cols: colNum,
    });
  },
  // setVal() {
  //   this.props.form.setFieldsValue({
  //     district: ['340000', '340800', '340822'],
  //   });
  // },
  render() {
    const { getFieldProps } = this.props.form;
    return (<div>
      <List style={{ backgroundColor: 'white' }}>
        <Picker 
        	extra="()" 
        	data={district} 
        	title="" 
        	okText="ok"
        	dismissText="cancel"
        	{...getFieldProps('district', {
          			
        		})
        	}
        	onPickerChange={this.onPickerChange}
        >
        	<List.Item arrow="horizontal" onClick={this.onClick}></List.Item>
        </Picker>
      </List>
    </div>);
  },
});

Test = createForm()(Test);

module.exports=React.createClass({
	getInitialState () {
		return {
			content:<div></div>
		}
	},
	signBtnClick:function(){
		Toast.loading('...', 1, () => {
			console.log('!!!');
		});
	},
	render () {
		return (<div>
			<WingBlank>
				<WhiteSpace size="sm" />
				<div className="content" dangerouslySetInnerHTML={{__html: this.state.content}} />
			    <WhiteSpace size="sm" />
			    <Button onClick={this.signBtnClick} className="bgColorRed"></Button>
			</WingBlank>
        </div>) 
	},
	componentDidMount:function () {
		this.setState({
			content:'<div><img src="http://img.fancyedu.com/sys/ic/operation/1476436649079_1_30.jpg"/> Progress \
			 ActivityIndicator </div>'
		});
	}
});


